@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
*{
  margin: 0;
  padding: 0;
  overflow-y: hidden;
}

h1, h2 h3, h4, h5, h6, p, a{
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {

}
nav{position:fixed;z-index:5;background-color:#008bf8;box-shadow:0 0 2px 2px rgba(0,0,0,.1);right:0;top:0;height:10vh;width:10vh;display:grid;place-items:center}.burger-wrapper{position:fixed;right:2.5vh;top:2.5vh;height:10vh;width:10vh;display:grid;place-items:center}.burger-container{z-index:1;overflow:visible;position:relative}.burger-1,.burger-2,.burger-3{margin-bottom:6px;width:30px;height:6px;border-radius:6px;background-color:#000;opacity:1;cursor:pointer;transition:opacity 300ms linear,-webkit-transform 300ms linear;transition:transform 300ms linear,opacity 300ms linear;transition:transform 300ms linear,opacity 300ms linear,-webkit-transform 300ms linear}.burger-3{margin-bottom:0}.show-nav .burger-1{position:absolute;left:0;top:50%;-webkit-transform:rotate(45deg);transform:rotate(45deg)}.show-nav .burger-2{opacity:0;visibility:hidden}.show-nav .burger-3{position:absolute;right:0;top:50%;-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}ul{overflow:hidden;height:80%;width:100%;visibility:hidden;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center}.show-nav ul{visibility:visible}li{width:80%;list-style:none;font-family:"Quicksand",sans-serif;font-weight:700;font-size:2rem;transition:all 200ms ease-in-out}li.selected{color:#f5ed00}li:hover{cursor:pointer;font-size:2.5rem}canvas{z-index:0}
.home{height:100vh;width:100%;background-color:none;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;background-color:#f0f0f0}.home h1{color:#04d259;font-size:5rem}.home h2{color:#04e762;font-size:3rem}.home h2 span{color:#04d259;font-style:italic}.home .opera-house{display:none;height:100vh;width:100vw;position:fixed;z-index:-1;background-color:blue}.home .opera-house .wing1{position:absolute;left:22%;bottom:0;background-color:#fff;height:65%;width:20%;border-radius:0 50% 0 0;border:solid #000 2px}.home .opera-house .wing2{position:absolute;left:35%;bottom:0;background-color:#fff;height:55%;width:20%;border-radius:0 50% 0 0;border:solid #000 2px}.home .opera-house .wing3{position:absolute;left:45%;bottom:0;background-color:#fff;height:45%;width:20%;border-radius:0 50% 0 0;border:solid #000 2px}.home .opera-house .wing4{position:absolute;left:50%;bottom:0;background-color:#fff;height:60%;width:20%;border-radius:50% 0 0 0;border:solid #000 2px}.home .opera-house .wing5{position:absolute;left:55%;bottom:0;background-color:#fff;height:45%;width:20%;border-radius:50% 0 0 0;border:solid #000 2px}
.about{height:100vh;width:100%;background-color:#f0f0f0;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;color:#04d259}.about h1{font-size:3rem}.about p{font-size:2rem}
.contact{height:100vh;width:100%;background-color:#f0f0f0;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;color:#04d259}.contact h1{font-size:3rem}
.scene{position:fixed;width:100%;background-color:#008bf8;transition:all 1s ease-in-out}
footer{position:fixed;height:10vh;width:100%;bottom:0;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.footer-wrapper{background-color:#e95c3a;height:100%;width:100%;display:grid;place-items:center;border-radius:5vw 5vw 0 0;border-top:1vh solid #c14b2e}.footer-wrapper{position:absolute}.footer-wrapper:hover{cursor:pointer;background-color:#fa6641}
